body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.filter {
    font-size: 16px;
    width: 100%;
}
.wrapper {
    background: #e68a3e;
    display: flex;
    flex-direction: column;
    align-items: center;
}

table {
    border-spacing: 0px;
    width: 100%;
}

th {
    text-align: start;
    padding: 5px;
    color: #443635;
}

td {
    padding: 5px;
    max-width: 700px;
    vertical-align: top;
    color: #443635;
}

tbody > tr:nth-child(even){
    background: #8ec0e8;
}

tbody > tr:nth-child(odd){
    background: #f9f778;
}
h1 {
    color: #443635
}

.number{
    text-align: center;
}
